.NewPost{
  form{
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    gap: 20px;
    padding: 20px 40px;

    input{
      border: none;
      padding: 1rem 0;
      font-size: 1.3rem;
      box-sizing: border-box;
      border-bottom: 1px solid #ccc;

      &::placeholder{
        font-size: 1.3rem;
      }

      &:focus{
        outline: none;
      }
    }

    .content{
      min-height: 400px;
      border-bottom: 1px solid #ccc;
      box-sizing: border-box;

      &:focus{
        outline: none;
      }
    }

    button{
      width: 100px;
      font-size: 1.2rem;
      align-self: flex-end;
      box-sizing: border-box;
      padding: .3rem 0;
      background-color: inherit;
      border: none;
      cursor: pointer;
      background-color: #333;
      color: #fff;
      border-radius: 20px;
      transition: .5s;

      &:hover{
        background-color: rgb(226, 147, 0);
      }
    }
  }
}